/*
 提示关注善诊公众号弹窗
*/

<template>
  <div class="dialog">
    <div class="member-dialog" v-if="isShow">
      <div class="member-dialog__mask" @click="close"></div>
        <official-account class="official_account" @load='loadOfficialAccount'></official-account>
      <div class="member-dialog-body">
        <button open-type="contact" class="member-dialog-body__btn">回复“砍价1”关注公众号看报告</button>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'szDialog',
  onLoad () {
    console.log(this.supportOfficialAccount)
  },
  data () {
    return {
      officialAccountShow: false
      // isShowDialog: false
    }
  },
  props: {
    isShow: {
      type: Boolean,
      default: false
    },
    supportOfficialAccount: {
      type: Boolean,
      default: false
    }
  },

  methods: {
    close () {
      this.$emit('close')
    }
  }
}
</script>

<style scoped lang="scss">
.official_account {
    position:absolute;
    top:0;
    width:100%;
}
.member-dialog {
  position: fixed;
  z-index: 101;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  &__mask {
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.7);
  }
  &-body {
    position: absolute;
    top: 45%;
    left: 50%;
    transform: translate(-50%, -50%);
    padding: rem(246) rem(80) 0 rem(80);
    background: url("https://static.shanzhen.me/images/mini-bargain/to-ylsg.png")
      top center no-repeat;
    background-size: contain;
    border-radius: rem(8);
    width: rem(627);
    height: rem(718);
    &__btn {
      position: absolute;
      top: 60%;
      left: 10%;
      border-radius: 40rpx;
      display: block;
      margin-top: rem(43);
      margin-bottom: rem(20);
      width: rem(489);
      height: rem(82);
      line-height: rem(80);
      text-align: center;
      background: $sz-color-red;
      color: #fff;
      font-size: rem(32);
    }
  }
}
</style>


