<template>
  <div class="to_off_acc">
    <official-account class="official_account" @load="loadOfficialAccount" @error="errorOfficialAccount"></official-account>
    <div class="text">
        <h2 class="special-text">预约体检</h2>
        <h2 class="special-text">请进入<span class="color-blue">“善诊”</span>公众号</h2> 
      <div class="special-next">为了保证您的个人信息安全，
        <br>预约/查看报告以及修改订单需进入公众号操作
      </div>
      <div :class="{'button':true,'button_show': !supportOfficialAccount}" >
        <button open-type="contact" class="special-btn" send-message-title='请按照客服提示预约体检' send-message-path='../detail/main' send-message-img='https://static.shanzhen.me/images/mini/wx-cooperate/toOfficialAccount/send-message-img.png' show-message-card=true>回复“预约”进入公众号</button>
      </div>
      <div class="bottom-image">
        <img
          src="https://static.shanzhen.me/images/mini/wx-cooperate/index/img-b.png"
          :class="{'taller': supportOfficialAccount}"
        ></div>
    </div>
  </div>
</template>

<script>
import szDialog from '@/components/szDialog-v2'
export default {
  name: 'to_official_account',
  components: { szDialog },
  data () {
    return {
      type:'',
      supportOfficialAccount: false
    }
  },
  onLoad() {
    let queryObj = this.$root.$mp.query
    this.type = queryObj.type
    this.$track('page_view', {
      page_name: '预约引导页',
      page_source: this.type,
      abtest:'测试A'
    })
  },
  methods: {
    loadOfficialAccount () {
      console.log('loadOfficialAccount')
      this.supportOfficialAccount = true
    },
    errorOfficialAccount () {
      console.log('errorOfficialAccount')
      this.supportOfficialAccount = false
    }
  }
}
</script>

<style scoped lang="scss">
.special-btn {
  width: 478rpx;
  display: flex;
  color: #ffffff;
  justify-content: center;
  align-items: center;
  background-color: #007AFF;
  border-radius: 200rpx !important;
}
.special-next {
  padding-left: 89rpx;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}
.special-text {
  width: 100%;
  margin-left: 89rpx;
}
.color-blue {
color: #007aff
}
.to_off_acc {
  .text {
    padding-top: 181rpx;
    h2 {
      font-family: PingFangSC-Semibold;
      text-align: left;
      font-size: 52rpx;
      color: #333333;
    }
    div {
      margin-top: 50rpx;
      font-family: PingFangSC-Regular;
      text-align: center;
      font-size: 28rpx;
      color: #999999;
    }
  }
  .button {
      display:flex;
      width: 478rpx;
      box-shadow: 0 2rpx 8rpx 0 #5BAAFF;
      justify-content: center;
      margin-top: 94rpx;
      background: #007AFF;
      line-height: 120rpx;
      text-align: center;
      color: white;
      margin: auto;
      border-radius: 49rpx;
  }
  .bottom-image {
    position: absolute;
    bottom: 0;
    width: 100%;
    font-size: 0!important;
    img {
      width: 100%;
      height: 452rpx;
    }
    .taller {
      height: 502rpx;
    }
  }
}
</style>



